	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Pox Be Gone","date":"23 Apr 2002","slug":"/2002-04-23-pox-be-gone/","tags":"Y2002"},"html":"<p>Christopher's chicken pox seems to be winding down. The fever has been gone for 24 hours, and the blisters are starting to dry up. It looks like his infliction was milder than normal (maybe because of good nursing from RN Grandma). And he might be caught up with his sleep again. And he's not contagious anymore, so we might be able to arrange that visit.</p>"},"next":{"fields":{"title":"Chicken Pox","date":"21 Apr 2002","slug":"/2002-04-21-chicken-pox/"}},"previous":{"fields":{"title":"Update From The Desert","date":"02 May 2002","slug":"/2002-05-02-update-from-the-desert/"}}},{"node":{"fields":{"title":"Chicken Pox","date":"21 Apr 2002","slug":"/2002-04-21-chicken-pox/","tags":"Y2002"},"html":"<p>We arrived in Las Vegas last evening, tired and cranky. And, for Christopher, with chicken pox. It was going around at day care two weeks ago, and we noticed the first blisters and a fever while on the plane. He's not that uncomfortable yet, but he must be highly contagious. So it probably won't be a good idea for us to visit Ian today. I've got the first nap duty, so I'm with him in the room trying out the cable modem at the hotel :-). </p>\n<p><b>Update</b> <a href=\"http://kidshealth.org/parent/infections/skin/chicken_pox.html\">Here's</a> everything you never wanted to know about chicken pox. Christopher is at starting to itch, but is still in relatively good spirits. He was up from midnight until exactly 5:18 am (with Mama's wake-up call at 6:30). Do I get extra Papa Points for staying up with him? Let's just say it would have been a hell of a lot more convenient to be going through this at home!</p>"},"next":{"fields":{"title":"On Our Way!","date":"19 Apr 2002","slug":"/2002-04-19-on-our-way/"}},"previous":{"fields":{"title":"Pox Be Gone","date":"23 Apr 2002","slug":"/2002-04-23-pox-be-gone/"}}},{"node":{"fields":{"title":"On Our Way!","date":"19 Apr 2002","slug":"/2002-04-19-on-our-way/","tags":"Y2002"},"html":"<p>We're packed up and ready to go! Departure from the house tomorrow is at 5:30 am, so we got to get to bed early and sleep well. Then 20 hours HAM-FRA-LAX-LAS. We arrive in Vegas tomorrow evening, and plan to crash at the hotel and become conscious again at about noon on Sunday. I hope Christopher sticks to this plan. He's already excited about \"Flugzeuge\" and \"airplanes\". Then again, he's excited about anything large and motorized, like bulldozers (\"Bagger!\") and tractors (\"Trecker!\"). I'll try to drop notes here from time to time. And we do plan to drop in on <a href=\"http://hal.editthispage.com/\">Hal, Audrey and Ian</a></p>"},"next":{"fields":{"title":"Not In My Backyard","date":"19 Apr 2002","slug":"/2002-04-19-not-in-my-backyard/"}},"previous":{"fields":{"title":"Chicken Pox","date":"21 Apr 2002","slug":"/2002-04-21-chicken-pox/"}}}],"pathPrefix":"","first":false,"last":false,"index":900,"pageCount":1101}}

